"use client";

import { useState, useEffect, useMemo } from 'react';
import { storage } from '../lib/storage/db';
import { MessageHandler } from '../lib/chat/message-handler';
import { Button } from './ui/button';
import { Input } from './ui/input';
import { Alert, AlertTitle, AlertDescription } from './ui/alert';
import { verifyOllamaSetup } from '../lib/ollama/connection';
import { ConversationList } from './conversation-list';
import { ModelRoleSwitcher } from './ui/model-role-switcher';
import type { Message, Conversation } from '../lib/storage/types';

export function ChatPanel() {
  const [currentConversation, setCurrentConversation] = useState<Conversation | null>(null);
  const [conversations, setConversations] = useState<Conversation[]>([]);
  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [isServerConnected, setIsServerConnected] = useState(false);
  const [lastConnectionCheck, setLastConnectionCheck] = useState(0);

  // Create a stable instance of MessageHandler
  const messageHandler = useMemo(() => new MessageHandler(storage), []);

  // Check Ollama server connection and verify model functionality
  useEffect(() => {
    async function verifyConnection() {
      // Only check if it's been more than 10 seconds since the last check
      const now = Date.now();
      if (now - lastConnectionCheck < 10000) {
        return;
      }
      
      setLastConnectionCheck(now);
      
      try {
        const result = await verifyOllamaSetup();
        setIsServerConnected(result.success);
        
        if (!result.success) {
          setError(result.error || 'Unable to connect to Ollama server or model test failed. Please ensure Ollama is running and the model is properly installed.');
        } else {
          setError(null);
        }
      } catch (error: any) {
        setIsServerConnected(false);
        setError(error.message || 'Ollama server not running or model test failed. Please start Ollama and ensure the model is installed.');
      }
    }
    
    verifyConnection();
    // Check connection every minute
    const interval = setInterval(verifyConnection, 60000);
    return () => clearInterval(interval);
  }, [lastConnectionCheck]);

  // Load all conversations on mount
  useEffect(() => {
    async function loadConversations() {
      try {
        const allConversations = await storage.getConversations();
        setConversations(allConversations);
        
        // If we have conversations, load the most recent one
        if (allConversations.length > 0) {
          await loadConversation(allConversations[0].id);
        } else {
          // Create a new conversation if none exist
          const newId = await storage.createConversation('New Chat', {
            responder: 'deepseek-r1:14b',
            listener: 'qwen2.5-coder:latest'
          });
          const conversations = await storage.getConversations();
          setConversations(conversations);
          const newConversation = conversations.find(conv => conv.id === newId);
          if (newConversation) {
            setCurrentConversation(newConversation);
            setMessages([]);
          }
        }
    } catch (error: any) {
      const errorMessage = error.message || 'Failed to initialize chat';
      console.error(errorMessage, error);
      setError(errorMessage);
      }
    }
    
    loadConversations();
  }, []);

  async function loadConversation(id: string) {
    try {
      const conversation = await storage.getConversation(id);
      if (!conversation) {
        throw new Error('Conversation not found');
      }
      
      const messages = await storage.getConversationMessages(id);
      setCurrentConversation(conversation);
      setMessages(messages);
    } catch (error: any) {
      const errorMessage = error.message || 'Failed to load conversation';
      console.error(errorMessage, error);
      setError(errorMessage);
    }
  }

  async function handleRoleSwitch() {
    if (!currentConversation || isLoading) return;

    setIsLoading(true);
    try {
      // Add a system message to mark the switch
      const switchMessage = await messageHandler.addSystemMessage(
        currentConversation.id,
        `Switching roles - ${currentConversation.models.listener} is now responding`
      );
      
      // Update the conversation roles in storage
      await storage.switchModelRoles(currentConversation.id);
      
      // Get the updated conversation
      const updatedConversation = await storage.getConversation(currentConversation.id);
      setCurrentConversation(updatedConversation);
      
      // Update messages to include the switch notification
      setMessages(prev => [...prev, switchMessage]);
    } catch (error: any) {
      const errorMessage = error.message || 'Failed to switch roles';
      console.error(errorMessage, error);
      setError(errorMessage);
    } finally {
      setIsLoading(false);
    }
  }

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    if (!input.trim() || isLoading || !currentConversation || !isServerConnected) return;

    setIsLoading(true);
    setError(null); // Clear any previous errors
    
    try {
      // First save the user's message
      const userMessage: Message = {
        id: Date.now().toString(),
        conversationId: currentConversation.id,
        role: 'user',
        content: input,
        timestamp: Date.now()
      };
      await storage.saveMessage(userMessage);
      setMessages(prev => [...prev, userMessage]);
      setInput(''); // Clear input after sending

      // Then process the message and get AI response
      try {
        await messageHandler.processNewMessage(currentConversation, input, 'user');
        // Reload messages to get the AI response
        const updatedMessages = await storage.getConversationMessages(currentConversation.id);
        setMessages(updatedMessages);
      } catch (error: any) {
        // If the AI response fails, show the error but keep the user's message
        const errorMessage = error.message || 'Failed to get AI response';
        console.error(errorMessage, error);
        setError(errorMessage);
        
        // Add a system message to indicate the error
        const errorSystemMessage: Message = {
          id: Date.now().toString(),
          conversationId: currentConversation.id,
          role: 'system',
          content: `Error: ${errorMessage}. Please try again.`,
          timestamp: Date.now()
        };
        await storage.saveMessage(errorSystemMessage);
        setMessages(prev => [...prev, errorSystemMessage]);
      }
    } catch (error: any) {
      const errorMessage = error.message || 'Failed to process message';
      console.error(errorMessage, error);
      setError(errorMessage);
    } finally {
      setIsLoading(false);
    }
  }

  return (
    <div className="flex h-full">
      <ConversationList
        conversations={conversations}
        currentId={currentConversation?.id || ''}
        onSelect={loadConversation}
      />
      <div className="flex flex-col flex-1 relative">
        {currentConversation && (
          <ModelRoleSwitcher
            models={currentConversation.models}
            onSwitch={handleRoleSwitch}
            disabled={isLoading}
          />
        )}
        <div className="flex-1 overflow-auto p-4 space-y-4">
          {error && (
            <Alert variant="destructive" className="mb-4">
              <AlertTitle>Error</AlertTitle>
              <AlertDescription>
                {error}
                {error.includes('deepseek-r1:14b') && (
                  <div className="mt-2">
                    <p>Run this command to load the DeepSeek model:</p>
                    <pre className="bg-gray-800 text-white p-2 mt-1 rounded">ollama pull deepseek-r1:14b</pre>
                  </div>
                )}
                {error.includes('qwen2.5-coder') && (
                  <div className="mt-2">
                    <p>Run this command to load the Qwen model:</p>
                    <pre className="bg-gray-800 text-white p-2 mt-1 rounded">ollama pull qwen2.5-coder</pre>
                  </div>
                )}
                {error.includes('not running') && (
                  <p className="mt-2">Make sure Ollama is installed and running on your system.</p>
                )}
              </AlertDescription>
            </Alert>
          )}
          {messages.map((message) => (
            <div
              key={message.id}
              className={`flex ${
                message.role === 'user' 
                  ? 'justify-end' 
                  : message.role === 'system' 
                    ? 'justify-center' 
                    : 'justify-start'
              }`}
            >
              <div
                className={`max-w-[80%] rounded-lg p-3 ${
                  message.role === 'user' 
                    ? 'bg-blue-500 text-white' 
                    : message.role === 'system'
                      ? 'bg-gray-200 text-gray-600 text-sm'
                      : 'bg-gray-100'
                }`}
              >
                {message.content}
              </div>
            </div>
          ))}
        </div>

        <form onSubmit={handleSubmit} className="p-4 border-t">
          <div className="flex gap-2">
            <Input
              value={input}
              onChange={(e) => setInput(e.target.value)}
              placeholder={isServerConnected ? "Type your message..." : "Connecting to Ollama server..."}
              disabled={isLoading || !isServerConnected}
            />
            <Button 
              type="submit" 
              disabled={isLoading || !isServerConnected}
              title={!isServerConnected ? "Waiting for Ollama server connection" : undefined}
            >
              {isLoading ? 'Sending...' : 'Send'}
            </Button>
          </div>
          {!isServerConnected && !error && (
            <Alert className="mt-4">
              <AlertTitle>Connecting...</AlertTitle>
              <AlertDescription>
                Attempting to connect to Ollama server...
              </AlertDescription>
            </Alert>
          )}
        </form>
      </div>
    </div>
  );
}
