import type { ChatMessage } from './types';

export class OllamaClient {
  async chat({ messages, model }: { messages: ChatMessage[]; model?: string }) {
    try {
      // Use the Next.js API route instead of direct connection
      const response = await fetch('/api/chat', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          model: model || 'deepseek-r1:14b',
          messages: messages,
          stream: false
        })
      });

      // If response isn't ok, get the error details
      if (!response.ok) {
        const errorData = await response.json();
        console.error('Ollama error details:', {
          status: response.status,
          statusText: response.statusText,
          error: errorData
        });
        throw new Error(`Ollama error: ${errorData.error || response.statusText}`);
      }

      const data = await response.json();
      console.log('Successful response:', data);
      return {
        content: data.message?.content || ''
      };
    } catch (error) {
      console.error('Detailed connection error:', error);
      throw error;
    }
  }

  // Add a simple test method
  async testConnection(model: string = 'deepseek-r1:14b') {
    try {
      const response = await fetch('/api/test-connection');
      const data = await response.json();
      
      if (!response.ok || !data.success) {
        console.error('Test failed:', data.error || response.statusText);
        return false;
      }
      
      console.log('Test response:', data);
      return true;
    } catch (error) {
      console.error('Test failed:', error);
      return false;
    }
  }
}

// Export a default instance
export const ollamaClient = new OllamaClient();
